const YouTubeTrend = forwardRef((_, youtubeRef) => {
  // const { youtubeList } = useSelector(state => state.trend);
  const youtubeList = youtubeJSON;
  // console.log(JSON.stringify(youtubeList));

  console.log(youtubeList);
  const contentsHtml = youtubeList.map((el, index) => (
    <Item key={el.videoId}>
      <a href={`${YOUTUBE_PLAY_URL}${el.videoId}`} target="_blank" rel="noopener noreferrer">
        <YoutubeThumbnail>
          <img src={el.imgURL} alt={el.imgURL} />
        </YoutubeThumbnail>
        <InfoWrapper>
          <YoutubeRanking>
            <RankingText>
              <span className="blind"></span>
              {index + 1}
            </RankingText>
          </YoutubeRanking>
          <MainInfo>
            <YoutubeTitle>{el.title}</YoutubeTitle>
            <YoutubeHost>{el.host}</YoutubeHost>
            <YoutubeView>
              <span className="blind"></span>
              {` ${el.view.replace("  ", "")}`}
            </YoutubeView>
          </MainInfo>
        </InfoWrapper>
      </a>
    </Item>
  ));

  return (
    <Section ref={youtubeRef}>
      <Wrapper>
        <h1 className="section__title">
          24   <br />
              .
        </h1>
        <List>{contentsHtml}</List>
      </Wrapper>
    </Section>
  );
});

export default YouTubeTrend;

const Section = styled.section`
  background-color: #fafafa;
`;

const Wrapper = styled.div`
  margin-bottom: 100px;
  margin-top: 100px;
  width: 980px;
`;

const List = styled.ol`
  display: flex;
  flex-wrap: wrap;
`;

const Item = styled.li`
  margin-bottom: 40px;
  width: 14rem;
  height: 14rem;
  white-space: normal;
  box-shadow: 2px 4px 12px rgb(0 0 0 / 8%);
  border-radius: 18px;
  margin-right: 20px;
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0, 0, 0.5, 1);

  &:nth-of-type(4n) {
    margin-right: 0;
  }

  &:hover {
    box-shadow: 2px 4px 16px rgb(0 0 0 / 16%);
    transform: scale3d(1.01, 1.01, 1.01);
  }
`;

const YoutubeThumbnail = styled.div`
  position: relative;

  & > img {
    width: 14rem;
  }
`;

const InfoWrapper = styled.div`
  margin-top: 10px;
  position: relative;
  height: 80px;
`;

const YoutubeRanking = styled.div`
  position: absolute;
  padding-left: 5px;
  top: 0;
  bottom: 1px;
  width: 20px;
  text-align: center;
`;

const RankingText = styled.span`
  display: block;
  font-size: 22px;
  line-height: 24px;
`;

const MainInfo = styled.div`
  display: -webkit-box;
  padding-right: 10px;
  margin-left: 35px;
  overflow: hidden;
  max-height: 40px;
  font-size: 15px;
  line-height: 18px;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  word-break: break-all;
  color: #090909;
`;

const YoutubeTitle = styled.div`
  display: block;
  max-height: 38px;
  white-space: nowrap;
  font-size: 1rem;
  overflow: hidden;
  word-break: break-all;
  text-overflow: ellipsis;
`;
const YoutubeHost = styled.div`
  margin-top: 5px;
  color: gray;
  display: block;
  font-size: 0.9rem;
`;

const YoutubeView = styled.div`
  position: absolute;
  bottom: 10px;
  padding-left: 18px;
  font-size: 14px;
  line-height: 20px;
  right: 15px;
`;
